.row {
    margin-left: 20px;
    color: white;
}
.row__posters {
   display: flex;
   overflow-y: hidden;
   overflow-x: scroll;
   padding: 20px;
}
.row__posters::-webkit-scrollbar {
    display: none;
}
.row__poster {
    object-fit: contain;
    width: 100%;
    max-height: 100px;
    margin-right: 10px;
    transition: transform 450ms;
}
.row__poster:hover {
    transform: scale(1.08);
}
.rowPosterLarge {
    max-height: 250px;
    object-fit: contain;
    width: 100%;
    margin-right: 10px;
    transition: transform 450ms;
}
.rowPosterLarge:hover {
    transform: scale(1.09);
}